import BadRequestException from '../exceptions/BadRequestException';

class RegexHelper {
    // constructor() {}

    success(id, msg2){
        document.getElementById(id).style.color='green';
        document.getElementById(id).innerHTML=msg2;
    }
    fail(id, msg){
        document.getElementById(id).style.color='red';
        document.getElementById(id).innerHTML=msg;
    }

    /**
     *    .
     * @param    {HTMLElement} field    <input> DOM 
     * @param    {string} msg         
     * @param    {string} msg2        
     * @param    {string} id          ref
     */
    value(field, msg, msg2, id) {
        const content = field.value;

        if (content === undefined || content === null || (typeof content === "string" && content.trim().length === 0)) {
            this.fail(id, msg);
            throw new BadRequestException(msg, field);
        } else {
            this.success(id, msg2);
        }

        return true;
    }

    /**
     *     .
     * @param    {HTMLElement} field    <input> DOM 
     * @param    {int} len        
     * @param    {string} msg         
     * @param    {string} msg2        
     * @param    {string} id        id
     */
    maxLength(field, len, msg, msg2, id) {
        this.value(field, msg);

        const content = field.value;
        this.fail(id, msg);
        if (content.trim().length > len) {
            throw new BadRequestException(msg, field);
        } else {
            this.success(id, msg2);
        }

        return true;
    }

    /**
     *     .
     * @param    {HTMLElement} field    <input> DOM 
     * @param    {int} len        
     * @param    {string} msg         
     * @param    {string} msg2        
     * @param    {string} id       id
     */
    minLength(field, len, msg, msg2, id) {
        this.value(field, msg);

        let content = field.value;
        this.fail(id, msg);

        if (content.trim().length < len) {
            throw new BadRequestException(msg, field);
        } else {
            this.success(id, msg2);
        }

        return true;
    }

    /**
     *    .
     * @param    {HTMLElement} field       CSS 
     * @param    {HTMLElement} compare       CSS 
     * @param    {string} msg         
     * @param    {string} msg2        
     * @param    {string} id       id
     */
    compareTo(field, compare, msg, msg2,  id) {
        this.value(field, msg, msg2, id);
        this.value(compare, msg, msg2, id);

        var src = field.value.trim(); //  .
        var dsc = compare.value.trim(); //   .

        if (src !== dsc) {
            this.fail(id, msg);
            throw new BadRequestException(msg, field);
        } else {
            this.success(id, msg2);
        }

        return true; //  
    }

    /**
     *     .
     * @param   {HTMLElement} field    <input> DOM 
     * @param    {string} msg         
     * @param    {string} msg2        
     */
    check(field, msg) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length === 0) {
            throw new BadRequestException(msg, field[0]);
        }
    }

    /**
     *      .
     * @param   {HTMLElement} field    <input> DOM 
     * @param    {number} len         
     * @param    {string} msg         
     * @param    {string} msg2        
     */
    checkMin(field, len, msg, msg2) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length < len) {
            throw new BadRequestException(msg, field);
        }
    }

    /**
     *      .
     * @param   {HTMLElement} field    <input> DOM 
     * @param    {number} len         
     * @param    {string} msg         
     * @param    {string} msg2        
     */
    checkMax(field, len, msg, msg2) {
        const checkedItem = Array.from(field).filter((v, i) => v.checked);

        if (checkedItem.length > len) {
            throw new BadRequestException(msg, field);
        }
    }

    /**
     *    .
     * @param    {HTMLElement} field    <input> DOM 
     * @param    {string} msg         
     * @param    {string} msg2        
     * @param    {string} id            id
     * @param    {object} regexExpr      
     */
    field(field, msg, msg2, id, regexExpr) {
        this.value(field, msg, msg2, id);

        //     ?
        if(!regexExpr.test(field.value.trim())) {
            this.fail(id, msg);
            throw new BadRequestException(msg, field);
        } else {
            this.success(id, msg2);
        }
        return true;
    }

    /**
     *  ,   ,        field()  .
     * @param {HTMLElement} field           CSS 
     * @param {string} msg               
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    navPw(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id,/^(?=.*?[A-Z])|(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{8,16}$/);
    }

    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    num(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id, /^[0-9]*$/);
    }

    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    eng(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id, /^[a-zA-Z]*$/);
    }

    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    kor(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id,/^[--]*$/);
    }

    /**
     *      field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    engNum(field, msg, msg2,  id) {
        return this.field(field, msg, msg2, id, /^[a-zA-Z0-9]*$/);
    }

    /**
     *      field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    korNum(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id,  /^[--0-9]*$/);
    }

    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    email(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id, /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i);
    }


    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    cellphone(field, msg, msg2, id) {
        return this.field(field, msg, msg2, id, /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
    }


    /**
     *     field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    telphone(field, msg,msg2, id) {
        return this.field(field, msg, msg2, id,/^\d{2,3}\d{3,4}\d{4}$/);
    }


    /**
     *            field()  .
     * @param   {HTMLElement} field    <input> DOM 
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    phone(field, msg,msg2, id) {
        this.value(field, msg, msg2, id);

        const content = field.value.trim();
        var check1 = /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/; //  
        var check2 = /^\d{2,3}\d{3,4}\d{4}$/; //  

        //          ?
        if(!check1.test(content) && !check2.test(content)) {
            field.focus();
            throw new BadRequestException(msg, field);
        } else {
            document.querySelector(id).innerHTML = msg2;
        }
        return true; //  
    }

    /**
     *    
     * @param {HTMLElement} field    <input> DOM 
     * @param {number} min        
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    min(field, min, msg, msg2, id) {
        if(field.value < min){
            field.focus();
            throw new BadRequestException(msg,field);
        } else {
            document.querySelector(id).innerHTML = msg2;
        }
    }

    /**
     *    
     * @param {HTMLElement} field    <input> DOM 
     * @param {number} max        
     * @param   {string} msg          
     * @param   {string} msg2         
     * @param   {string} id         id
     */
    max(field, max, msg, msg2, id) {
        if(field.value > max){
            field.value = max;
            field.focus();
            throw new BadRequestException(msg,field);
        } else {
            document.querySelector(id).innerHTML = msg2;
        }
    }
}

export default new RegexHelper();
